import React, { useState, useEffect } from "react";
import "./App.css";
import axios from "axios";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement, // 
  LineElement, // 
  Tooltip,
  Legend,
  TimeScale,
} from "chart.js";
import { Line } from "react-chartjs-2";

// 
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  TimeScale,
  Tooltip,
  Legend
);

function App() {
  // 
  const [endpoint, setEndpoint] = useState("dayData");
  const [jsonData, setJsonData] = useState(); //DayJson

  console.log(`${endpoint}`);

  useEffect(() => {
    axios
      .get(`http://0.0.0.0:5000/api/${endpoint}`)
      .then((response) => {
        console.log("Received data:", response.data); // 
        setJsonData(response.data); // 
      })
      .catch((error) => console.error("Error fetching data:", error));
  }, [endpoint]);

  useEffect(() => {
    // 
    const chartWrapper = document.querySelector(".chart-wrapper");
    if (chartWrapper) {
      chartWrapper.scrollLeft = chartWrapper.scrollWidth;
    }
  }, [jsonData]);

  //
  if (!jsonData) {
    return <div>Loading...</div>;
  }

  // jsonData
  // console.log("jsonData:", jsonData);

  const labels = jsonData.labels.time;

  const graphData = {
    labels: labels,
    datasets: [
      {
        label: "",
        data: jsonData.datasets.temperature,
        borderColor: "rgb(75, 192, 192)",
      },
    ],
  };

  const options = {
    maintainAspectRatio: false,
    y: {
      position: "right", // 
      min: 0, // Y
      max: 35, // Y
    },
    plugins: {
      legend: {
        display: false, // 
      },
    },
  };

  const labelCount = graphData.labels.length; // 
  const labelWidth = 30; // (px)
  const canvasWidth = labelCount * labelWidth; //(px)

  return (
    <div className="App">
      <header className="header">
        <p className="header-title"></p>
      </header>
      <div className="container">
        <div className="btn-wrapper">
          <button
            className={`select-btn ${endpoint === "dayData" ? "active" : ""}`} // endpoint === DayJsonactive
            onClick={() => {
              setEndpoint("dayData");
            }}
          >
            
          </button>
          <button
            className={`select-btn ${endpoint === "weekData" ? "active" : ""}`}
            onClick={() => {
              setEndpoint("weekData");
            }}
          >
            
          </button>
          <button
            className={`select-btn ${endpoint === "monthData" ? "active" : ""}`}
            onClick={() => {
              setEndpoint("monthData");
            }}
          >
            
          </button>
          <button
            className={`select-btn ${endpoint === "yearData" ? "active" : ""}`}
            onClick={() => {
              setEndpoint("yearData");
            }}
          >
            
          </button>
        </div>
        <div className="chart-container">
          <p className="chart-title">{jsonData.title}</p>
          <div className="chart-wrapper">
            <div className="chart-box" style={{ width: `${canvasWidth}px` }}>
              <Line
                data={graphData}
                options={options}
                className="chart"
                id="chart-key"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
